const article = [
    ['article', {attr:'article', class:'article'}],
        ['h2',  {class:'article_heading'}], {var: 'title'}, ['/h2'],
        ['p',   {class:'article_author'} ], 'author: ', {var: 'author'}, ['/p'],
        ['div', {class:'article_content'}], 
            {var: 'content'}, 
        ['/div'],
    ['/article']
]

export default article